.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1 {
  font-size: 30px;
  color: darkslategray;
}
h4 {
  font-size: 35px;
  font-style: italic;
}
h2 {
  font-size: 25px;
  color: darkslateblue;
}
img {
  width: 80%;
}
blockquote {
  width: 80%;
}

#loading {
  width: 200px;
  height: 200px;
}
